import * as THREE from 'three';
import { loader } from '../engine';

import BG from '../assets/bg.png';

const background = new THREE.PlaneGeometry(window.innerWidth, window.innerHeight);

const targetAspect = window.innerWidth / window.innerHeight;
const imageAspect = 3840 / 2160;
const factor = imageAspect / targetAspect;

const bgTex = loader.load(BG);
bgTex.wrapS = THREE.MirroredRepeatWrapping;

// When factor larger than 1, that means texture 'wilder' than target。 
// we should scale texture height to target height and then 'map' the center  of texture to target， and vice versa.
bgTex.offset.x = factor > 1 ? (1 - 1 / factor) / 2 : 0;
bgTex.repeat.x = factor > 1 ? 1 / factor : 1;
bgTex.offset.y = factor > 1 ? 0 : (1 - factor) / 2;
bgTex.repeat.y = factor > 1 ? 1 : factor;

const bgMaterial = new THREE.MeshBasicMaterial( { map: bgTex } );
const bgObject = new THREE.Mesh( background, bgMaterial );
bgObject.position.z = -1000;

export {
  bgObject,
  bgTex,
};
