import * as THREE from 'three';
import { randFloat, randInt } from 'three/src/math/MathUtils';

import { clock, loader } from '../engine';
import { scene } from '../engine';

import Explosion1 from '../assets/FX/explosion-1.png';
import Explosion2 from '../assets/FX/explosion-2.png';
import Explosion3 from '../assets/FX/explosion-3.png';
import Explosion4 from '../assets/FX/explosion-4.png';

let particles: any[] = [];

const ex1 = loader.load(Explosion1);
ex1.offset = new THREE.Vector2(0.0, 0.0);
ex1.repeat.x = 1.0 / 8;

const ex2 = loader.load(Explosion2);
ex2.offset = new THREE.Vector2(0.0, 0.0);
ex2.repeat.x = 1.0 / 8;

const ex3 = loader.load(Explosion3);
ex3.offset = new THREE.Vector2(0.0, 0.0);
ex3.repeat.x = 1.0 / 10;

const ex4 = loader.load(Explosion4);
ex4.offset = new THREE.Vector2(0.0, 0.0);
ex4.repeat.x = 1.0 / 12;

const explosionList = [{
  tex: Explosion1,
  count: 8,
}, {
  tex: Explosion2,
  count: 8,
}, {
  tex: Explosion3,
  count: 10,
}, {
  tex: Explosion4,
  count: 12,
}];

const doExplosion = (spawnPoint: THREE.Vector3, count: number) => {
  particles.forEach((particle) => {
    scene.remove(particle);
  });
  particles = [];

  for (let i = 0; i < count; ++i) {
    const exIndex = randInt(0, 3);

    const explode = new THREE.PlaneBufferGeometry(64, 64);
    const ex = loader.load(explosionList[exIndex].tex);
    ex.offset = new THREE.Vector2(0.0, 0.0);
    ex.repeat.x = 1.0 / explosionList[exIndex].count;
    ex.needsUpdate = true;
    const exMaterial = new THREE.MeshBasicMaterial( { map: ex, transparent: true } );
    const explodeMesh = new THREE.Mesh( explode, exMaterial );

    explodeMesh.position.x = spawnPoint.x + randFloat(80, 100);
    explodeMesh.position.y = spawnPoint.y + randFloat(50, 100);
    const scaleFactor = randFloat(4, 5);
    explode.scale(scaleFactor, scaleFactor, 1);
  
    scene.add(explodeMesh);
  
    particles.push({
      tex: ex,
      count: explosionList[exIndex].count,
      explodeMesh,
      time: randFloat(-1, 0),
    });
  }
};

const updateExplosion = () => {
  particles.forEach((particle) => {
    if (!particle.dead) {
      particle.time += clock.getDelta() * 3000;
      if (particle.time > 0.3) {
        particle.time = 0;
        particle.tex.offset.x = particle.tex.offset.x + (1.0 / particle.count);
        particle.tex.needsUpdate = true;
      }
      if (particle.tex.offset.x > 0.99) {
        particle.dead = true;
        scene.remove(particle.explodeMesh);
      }
    }
  });
}


export {
  doExplosion,
  updateExplosion,
};
