import * as THREE from 'three';
import { clock, loader } from '../engine';

import Eye from '../assets/Char/eye.png';
import Face from '../assets/Char/face.png';
import Hair from '../assets/Char/hair.png';
import Mouth from '../assets/Char/mouth.png';

import Body from '../assets/Char/body.png';
import Pants from '../assets/Char/pants.png';
import Hand from '../assets/Char/hand.png';
import Leg from '../assets/Char/leg.png';
import Weapon from '../assets/Char/weapon.png';

import { camera, cameraInitialPos } from '../engine';
import { bgTex } from '../game/background';
import { doExplosion } from './particlesystem';

let isAttacking = false;
let isEmittedParticles = false;
let animationTime = 0;

const SCALE_FACTOR = window.innerWidth < 992 ? 0.7 : 1;
const DEFAULT_HEAD_OFFSET = 50;

const head = new THREE.PlaneGeometry(750, 750);
const headTex = loader.load(Face);
const headMaterial = new THREE.MeshBasicMaterial( { map: headTex, transparent: true } );
const headMesh = new THREE.Mesh( head, headMaterial );
headMesh.position.z = 0;

const hair = new THREE.PlaneGeometry(750, 750);
const hairTex = loader.load(Hair);
const hairMaterial = new THREE.MeshBasicMaterial( { map: hairTex, transparent: true } );
const hairMesh = new THREE.Mesh( hair, hairMaterial );
hairMesh.position.z = 1;

const eye = new THREE.PlaneGeometry(346, 200);
const eyeTex = loader.load(Eye);

eyeTex.offset = new THREE.Vector2(0.0, 0.666);
eyeTex.repeat.y = 0.333;

const eyeMaterial = new THREE.MeshBasicMaterial( { map: eyeTex, transparent: true } );
const eyeMesh = new THREE.Mesh( eye, eyeMaterial );
eyeMesh.position.set(50, -80, 0);

const mouth = new THREE.PlaneGeometry(180, 180);
const mouthTex = loader.load(Mouth);
const mouthMaterial = new THREE.MeshBasicMaterial( { map: mouthTex, transparent: true } );
const mouthMesh = new THREE.Mesh( mouth, mouthMaterial );

mouthTex.offset = new THREE.Vector2(0.0, 0.5);
mouthTex.repeat.y = 0.5;

mouthMesh.position.set(50, -210, 0);

const headMain = new THREE.Object3D();

headMain.add(mouthMesh);
headMain.add(headMesh);
headMain.add(eyeMesh);
headMain.add(hairMesh);

headMain.position.z = 5;
headMain.scale.multiplyScalar(1.3 * SCALE_FACTOR * 1.1);

const body = new THREE.PlaneGeometry(750, 750);
const bodyTex = loader.load(Body);
const bodyMaterial = new THREE.MeshBasicMaterial( { map: bodyTex, transparent: true } );
const bodyMesh = new THREE.Mesh( body, bodyMaterial );
bodyMesh.position.z = 1;

const hand = new THREE.PlaneGeometry(750, 750);
const handTex = loader.load(Hand);
const handMaterial = new THREE.MeshBasicMaterial( { map: handTex, transparent: true } );
const leftArmMesh = new THREE.Mesh( hand, handMaterial );
const rightArmMesh = new THREE.Mesh( hand, handMaterial );

const leftArm = new THREE.Object3D();
leftArm.add(leftArmMesh);

leftArmMesh.position.y = -100;

leftArm.position.z = 2;
leftArm.position.y = 130;
leftArm.position.x = 110;
leftArm.rotation.z = 0.9;

const rightArm = new THREE.Object3D();
rightArm.add(rightArmMesh);

rightArmMesh.position.y = -100;

rightArm.position.z = 2;
rightArm.position.y = 130;
rightArm.position.x = -110;
rightArm.rotation.z = 0.7;

const weapon = new THREE.PlaneGeometry(750, 1500);
const weaponTex = loader.load(Weapon);
const weaponMaterial = new THREE.MeshBasicMaterial( { map: weaponTex, transparent: true } );
const weaponMesh = new THREE.Mesh( weapon, weaponMaterial );

const weaponMain = new THREE.Object3D();
weaponMain.add(weaponMesh);

weaponMesh.position.y = 570;

weaponMain.position.z = 10;
weaponMain.position.y = -210;
weaponMain.position.x = 30;
weaponMain.rotation.z = -1.9;

rightArm.add(weaponMain);

const bodyMain = new THREE.Object3D();
bodyMain.add(bodyMesh);
bodyMain.add(leftArm);
bodyMain.add(rightArm);
bodyMain.add(headMain);

// bodyMain.rotation.z = 0.7;

// bodyMain.add(pantsMesh);

bodyMain.position.y = DEFAULT_HEAD_OFFSET * 1.1;
bodyMain.scale.multiplyScalar(0.25 * SCALE_FACTOR);
bodyMain.position.z = -50;



const pants = new THREE.PlaneGeometry(750, 750);
const pantsTex = loader.load(Pants);
const pantsMaterial = new THREE.MeshBasicMaterial( { map: pantsTex, transparent: true } );
const pantsMesh = new THREE.Mesh( pants, pantsMaterial );
pantsMesh.position.y = 50;
pantsMesh.position.z = 2;

const leg = new THREE.PlaneGeometry(750, 750);
const legTex = loader.load(Leg);
const legMaterial = new THREE.MeshBasicMaterial( { map: legTex, transparent: true } );
const rightlegMesh = new THREE.Mesh( leg, legMaterial );
rightlegMesh.position.y = -90;
rightlegMesh.position.x = -50;
rightlegMesh.position.z = 1;
rightlegMesh.rotation.z = 0.1;

const leftlegMesh = new THREE.Mesh( leg, legMaterial );
leftlegMesh.position.y = -65;
leftlegMesh.position.x = 80;
leftlegMesh.position.z = 1;
leftlegMesh.rotation.z = 0.1;


const lowerBody = new THREE.Object3D();
lowerBody.add(leftlegMesh);
lowerBody.add(rightlegMesh);
lowerBody.add(pantsMesh);
lowerBody.scale.multiplyScalar(0.25 * SCALE_FACTOR);
lowerBody.position.y = 55;
lowerBody.position.z = -51;
lowerBody.rotation.z = -0.2;



const char = new THREE.Object3D();
char.add(bodyMain);
char.add(lowerBody);


headMain.children.forEach((child) => {
  if (child.type === 'Mesh') {
    child.position.y += 375;
  }
});

char.position.x -= 0.3 * window.innerWidth;
char.position.y -= 0.2 * window.innerHeight;

const doCharIdleAnimation = () => {
  const elapsedTime = clock.getElapsedTime();
  bodyMain.position.y = DEFAULT_HEAD_OFFSET * 1.1 + Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 10) * 5;
  lowerBody.position.y = 55 - Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 10) * 0.3;
  headMain.rotation.z =  Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 10) * 0.05;

  if ((elapsedTime * 10) % 20 * 10 > 10) {
    eyeTex.offset = new THREE.Vector2(0.0, 0.666);
  } else if ((elapsedTime * 10) % 20 * 10 > 3) {
    eyeTex.offset = new THREE.Vector2(0.0, 0.333);
  } else {
    eyeTex.offset = new THREE.Vector2(0.0, 0.0);
  }
};

const setInitialPosition = () => {
  mouthTex.offset = new THREE.Vector2(0.0, 0.5);

  char.position.x = -0.3 * window.innerWidth;
  char.position.y = -0.2 * window.innerHeight;
  char.rotation.z = 0;

  leftArmMesh.position.y = -100;
  leftArm.position.y = 130;
  leftArm.position.x = 110;
  leftArm.rotation.z = 0.9;

  rightArmMesh.position.y = -100;
  rightArm.position.y = 130;
  rightArm.position.x = -110;
  rightArm.rotation.z = 0.7;

  bodyMain.position.y = DEFAULT_HEAD_OFFSET * 1.1;

  pantsMesh.position.y = 50;

  rightlegMesh.position.y = -90;
  rightlegMesh.position.x = -50;
  rightlegMesh.rotation.z = 0.1;

  leftlegMesh.position.y = -65;
  leftlegMesh.position.x = 80;
  leftlegMesh.rotation.z = 0.1;

  lowerBody.position.y = 55;
  lowerBody.rotation.z = -0.2;
}

const doToggleMouth = () => {
  mouthTex.offset = mouthTex.offset.y === 0.0 ? new THREE.Vector2(0.0, 0.5) : new THREE.Vector2(0.0, 0.0);
}

const doAttack = () => {
  isAttacking = true;
  isEmittedParticles = false;
  animationTime = 0;
  setInitialPosition();
};

const initialY =  -0.2 * window.innerHeight;
const initalX = -0.3 * window.innerWidth;

const targetPosX = 0.2 * window.innerWidth;

const doCharAttackAnimation = () => {
  const time = clock.getDelta();
  mouthTex.offset = new THREE.Vector2(0.0, 0.0);


  // Move to target
  if (animationTime < 3) {
    if (animationTime <= 2.8) {
      char.position.y = initialY + Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 5;
    }
   
    char.position.x = initalX + (targetPosX - initalX) * (animationTime / 3);

    lowerBody.position.y = 55 - Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 10) * 0.3;
    leftlegMesh.rotation.z =  -Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 0.1;
    rightlegMesh.rotation.z =  Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 0.2;

    rightArm.rotation.z = -0.7 + Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 0.02;
    weaponMain.rotation.z = -3.7;

    bgTex.offset.x += time * 0.3;
  }

  if (animationTime > 2.7 && animationTime <= 3) {
    const duration = 3 - 2.7;
    const targetPosY = initialY + 300;

    char.position.y = initialY + (targetPosY - initialY) * ((animationTime - 2.7) / duration);
    rightArm.rotation.z = -0.3 + Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 0.02;

    bgTex.offset.x += time * 0.3;
  }

  if (animationTime > 3 && animationTime < 3.5) {
    const initialDuration = animationTime - 3;
    const duration = 0.5;

    char.rotation.z = -(initialDuration / duration) * 720 * Math.PI / 180;
    // rightArm.rotation.z = -0.3 + Math.sin(15 * Math.PI / 180 * clock.getElapsedTime() * 100) * 0.02;

    bgTex.offset.x += time * 0.3;
  }

  if (animationTime > 3 && animationTime < 3.3) {
    const duration = 0.3;

    const targetPosY = initialY + 300;

    char.position.y = targetPosY + (initialY - targetPosY) * ((animationTime - 3) / duration);

    // char.rotation.z = -(initialDuration / duration) * 720 * Math.PI / 180;
    rightArm.rotation.z = -1.6;

    bgTex.offset.x += time * 0.3;
  }

  if (animationTime > 3.3 && animationTime < 3.6) {
    weaponMain.rotation.z = -1.9;
    const duration = 0.3;
    // char.rotation.z = -(initialDuration / duration) * 720 * Math.PI / 180;
    rightArm.rotation.z = -1.6 + (-5) * ((animationTime - 3.3) / duration);
    camera.zoom = 1 + 0.2 * ((animationTime - 3.3) / duration);
    camera.updateProjectionMatrix();

    bgTex.offset.x += time * 0.3;
  }

  if (animationTime > 3.4 && animationTime < 5) {
    camera.position.y = cameraInitialPos.y + Math.sin(Math.PI / 180 * animationTime * 1000) * 20;
    camera.position.x = cameraInitialPos.x + Math.cos(Math.PI / 180 * animationTime * 1000) * 20;

    bgTex.offset.x += time * 0.3;

    if (!isEmittedParticles) {
      isEmittedParticles = true;
      doExplosion(new THREE.Vector3(targetPosX, initialY, 0), 1);
    }
  }


  if (animationTime > 4 && animationTime < 4.5) {
    const targetPosX = 0.3 * window.innerWidth;
    char.position.x = targetPosX + (initalX - targetPosX) * ((animationTime - 4) * 2);
    camera.zoom = 1.2 - 0.2 * ((animationTime - 4) * 2);
    camera.updateProjectionMatrix();

    bgTex.offset.x += time * 0.1;
  }

  if (animationTime >= 4.5) {
    isAttacking = false;
    setInitialPosition();
    camera.position.copy(cameraInitialPos);
  }

  animationTime += time;
};



export {
  char,
  isAttacking,
  doCharIdleAnimation,
  doCharAttackAnimation,
  doToggleMouth,
  doAttack,
};
