import React, { useEffect } from 'react';

import {
  renderer,
  scene,
  camera,
  updateCamera,
} from './engine';

import { bgObject } from './game/background';
import { char, isAttacking, doCharIdleAnimation, doCharAttackAnimation, doToggleMouth, doAttack } from './game/character';
import { updateExplosion } from './game/particlesystem';

import styles from './App.module.scss';

let isFullScreen = false;

const resizeRenderer = () => {
  // var elem = document.getElementById("id-webglcanvas");

  // if (isFullScreen && elem) {
  //   var sceneWidth = window.innerWidth;
  //   var sceneHeight = elem.offsetHeight;
  //   renderer.setSize( sceneWidth, sceneHeight );
  //   camera.updateProjectionMatrix();
  // }
  
  renderer.setSize( window.innerWidth, window.innerHeight );
  updateCamera();
};

const openFullscreen = () => {
  const elem: any = document.getElementById("id-webglcanvas");

  if (elem) {
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.mozRequestFullScreen) { /* Firefox */
      elem.mozRequestFullScreen();
    } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari & Opera */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) { /* IE/Edge */
      elem.msRequestFullscreen();
    }
    elem.style.width = '100%';
    elem.style.height = '100%';

    isFullScreen = true;

    resizeRenderer();
  }
}

const App = () => {
  useEffect(() => {
    resizeRenderer();
    document.body.appendChild( renderer.domElement );

    scene.add(bgObject);
    scene.add(char);

    const animate = function () {
      isAttacking ? doCharAttackAnimation() : doCharIdleAnimation();
      updateExplosion();

      requestAnimationFrame( animate );
      renderer.render( scene, camera );
    };

    animate();
  }, []);

  useEffect(() => {
    window.addEventListener("resize", resizeRenderer);
    return () => window.removeEventListener("resize", resizeRenderer);
  }, []);


  return (
    <div id="id-webglcanvas">
      <div className={styles.ui}>
        <button className={styles.mouth} type="button" onClick={doToggleMouth}>
          Mouth
        </button>
        <button className={styles.attack} type="button" onClick={doAttack}>
          Attack
        </button>
        {/* <button className={styles.fullscreen} type="button" onClick={openFullscreen}>
          Polygon
        </button> */}
      </div>
    </div>
  );
}

export default App;
