import * as THREE from 'three';

let w = window.innerWidth;
let h = window.innerHeight;
let viewSize = h;
let aspectRatio = w / h;
let cameraInitialPos: THREE.Vector3;

let viewport = {
  viewSize: viewSize,
  aspectRatio: aspectRatio,
  left: (-aspectRatio * viewSize) / 2,
  right: (aspectRatio * viewSize) / 2,
  top: viewSize / 2,
  bottom: -viewSize / 2,
  near: 0,
  far: 1000,
}

const camera = new THREE.OrthographicCamera(
  viewport.left,
  viewport.right,
  viewport.top,
  viewport.bottom,
  viewport.near,
  viewport.far
);

const updateCamera = () => {
  w = window.innerWidth;
  h = window.innerHeight;
  viewSize = h;
  aspectRatio = w / h;

  viewport = {
    viewSize: viewSize,
    aspectRatio: aspectRatio,
    left: (-aspectRatio * viewSize) / 2,
    right: (aspectRatio * viewSize) / 2,
    top: viewSize / 2,
    bottom: -viewSize / 2,
    near: 0,
    far: 1000,
  };

  camera.left = viewport.left;
  camera.right = viewport.right;
  camera.top = viewport.top;
  camera.bottom = viewport.bottom;  
}


cameraInitialPos = new THREE.Vector3().copy(camera.position);

export {
  updateCamera,
  cameraInitialPos,
  camera,
}
